import './App.css';
import React, { useState, useEffect, useRef } from "react"
import axios from 'axios';
import Delete from './component/delete';
import Put from './component/put';
function App() {

  const [data, setData] = useState([])
  const [group, setGroup] = useState([])
  const [messeg, setMesseg] = useState("")
  const [name , setName] = useState([])
  const valu  = useRef()

  useEffect(() => {
    axios.get("https://chat-json-api.herokuapp.com/GroupsId")
      .then(respons => {
        setData(respons.data)
      })
  }, [])
  useEffect(() => {
    axios.get(`https://chat-json-api.herokuapp.com/GroupsId/${localStorage.getItem("id")}`)
      .then(respons => {
        setName(respons.data)
      })
  }, [name])

  useEffect(() => {
    axios.get(`https://chat-json-api.herokuapp.com/Item${localStorage.getItem("id")}`)
      .then(res => {
        setGroup(res.data)
      })
  }, [group])

  const HendelChange = () => {
    const messegContent = {
      messeg: messeg
    }
    const respons = axios.post(`https://chat-json-api.herokuapp.com/Item${localStorage.getItem("id")}`, messegContent)
    console.log(respons);
    valu.current.value = ""
  } 


  return (
    <>
      <div className='container'>
        {data.map((elem, index) =>
          <div className='div' key={index}>
            <button key={index} onClick={() => { localStorage.setItem("id", elem.id) }}>{elem.title}</button>
          </div>
        )}
        <p key={name.id} className='name'>{name.title}</p>
        {group == 0 ? <><h2 >Messeg end</h2></> : null }
        {group.map((els, index) =>
          <>
            <div className='divs' key={index}>
              <Put putId={els.id} />
              <div key={index}>
                <p className='subtext' key={index} >{els.messeg}</p>
                <Delete delId={els.id} />
              </div>
            </div>

          </>
        )}

        <input ref={valu} className='sendInput' placeholder='messeg...' onChange={(e) => { setMesseg(e.target.value) }} plcholder="messeg..." type="text" />
        <button className='sendBut' onClick={HendelChange}>send</button>

      </div>
    </>
  );
}

export default App;
