import "./styles.css"
import axios from "axios";


function Delete({delId}) {

    const HendelDelet = (e) => {
        const response = axios.delete(`https://chat-json-api.herokuapp.com/Item${localStorage.getItem("id")}/${e.target.value}`)
        console.log(response);
    }

    return (
        <>
            <button value={delId} onClick={HendelDelet} className="delButton">Delete</button>
        </>
    );
}


export default Delete;