import * as React from 'react';
import Box from '@mui/material/Box';
import Modal from '@mui/material/Modal';
import axios from 'axios';
import "./styles.css"

const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 400,
    bgcolor: 'background.paper',
    border: '2px solid #000',
    boxShadow: 24,
    p: 4,
};

function Put({ putId }) {
    const input = React.useRef()
    const [open, setOpen] = React.useState(false);
    const handleOpen = () => setOpen(true);
    const handleClose = () => setOpen(false);

    const HendelPut = (e) => {
        const body = {
            messeg: input.current.value
        }
        const response = axios.put(`https://chat-json-api.herokuapp.com/Item${localStorage.getItem("id")}/${e.target.value}`, body)
        console.log(response);
        setOpen(false)
    }
    return (
        <>
            <div>
                <button className='put' onClick={handleOpen}>Edit Messeg</button>
                <Modal
                    open={open}
                    onClose={handleClose}
                    aria-labelledby="modal-modal-title"
                    aria-describedby="modal-modal-description"
                >
                    <Box sx={style}>
                        <input className='editInput' ref={input} type="text" placeholder='edit...' />
                        <button className='editBut' onClick={HendelPut} value={putId}>Put</button>
                    </Box>
                </Modal>
            </div>
        </>
    );
}


export default Put;